<template>
  <div class="find">
  	<header>
  		<yd-search v-model="value1" @input='get'></yd-search>
  		<ul v-show="flag">
  			<li v-for="(value,index) in searchList" @click="searchLink(value)">{{value}}</li>
  		</ul>
  	</header>
  	<nav>
  		<yd-slider autoplay="3000">
        <yd-slider-item>
            <a href="#">
                <img src="../assets/img/img20.png">
            </a>
        </yd-slider-item>
        <yd-slider-item>
            <a href="#">
                <img src="../assets/img/img21.png">
            </a>
        </yd-slider-item>
        <yd-slider-item>
            <a href="#">
                <img src="../assets/img/img22.png">
            </a>
        </yd-slider-item>
               <yd-slider-item>
            <a href="#">
                <img src="../assets/img/img23.png">
            </a>
        </yd-slider-item>
    </yd-slider>    
  		<ul class="confirmer">
  			<li>
  				<span class="iconfont">&#xe73f;</span>
  				<span>电子书</span>
  			</li>
  			<li>
  				<span class="iconfont">&#xe640;</span>
  				<span>每天听本书</span>
  			</li>
  			<li>
  				<span class="iconfont">&#xe8fd;</span>
  				<span>商城</span>
  			</li>
  			
  			<li>
  				<span class="iconfont">&#xe606;</span>
  				<span>系列</span>
  			</li>
  			<li>
  				<span class="iconfont">&#xe607;</span>
  				<span>全部</span>
  			</li>
  		</ul>
  	</nav>
  	<div class="main">
  		<div>
  			<h2>
  				<span>免费专区</span>
  				<span class="iconfont">&#xe62e;连续播放</span>
  			</h2>
  			<ul>
  				<h2>
  					<span>李翔知识内参</span>
  					<span>查看全部 ></span>
  				</h2>
  				<li>
  					<span class="iconfont">&#xe62e;</span>
  					<span>{{msg}}</span>
  				</li>
  				<li>
  					<span class="iconfont">&#xe62e;</span>
  					<span>{{msg1}}</span>
  				</li>
  				<li>
  					<span class="iconfont">&#xe62e;</span>
  					<span>{{msg2}}</span>
  				</li>
  				<li>
  					<span class="iconfont">&#xe62e;</span>
  					<span>{{msg3}}</span>
  				</li>
  			</ul>
  			<ul>
  				<h2>
  					<span>罗辑思维</span>
  					<span>查看全部 ></span>
  				</h2>
  				<li>
  					<span class="iconfont">&#xe62e;</span>
  					<span>{{msg4}}</span>
  				</li>
  			</ul>
  		</div>
  	</div>
  </div>
</template>

<script>
	import Vue from 'vue';
	import {Slider, SliderItem} from 'vue-ydui/dist/lib.rem/slider';
	import {Search} from 'vue-ydui/dist/lib.rem/search';	
	
	Vue.component(Search.name, Search);
	Vue.component(Slider.name, Slider);
	Vue.component(SliderItem.name, SliderItem);
export default {
	data(){
		return {
			msg:'方法：如何培养灵感',
			msg1:'为什么中国公司缺乏研发能力',
			msg2:'费德勒：有时候“少就是多”',
			msg3:'张瑞敏：让每个人都成为张瑞敏',
			msg4:'第369期 | 优势反制',
			value1:'',
			searchList:[],
			flag:false,
		}
	},
	methods:{
		 get(evt) {
            if(evt.keyCode == 38 || evt.keyCode == 40) return
            
            this.$http.jsonp("https://sp0.baidu.com/5a1Fazu8AA54nxGko9WTAnF6hhy/su",{//跨域请求数据
                params: {
                    wd: this.value1//输入的关键词
                },
                jsonp: 'cb'//这里是callback
            }).then((res) => {//请求成功回调，请求来的数据赋给searchList数组
                this.searchList = res.body.s;
                this.flag=true;               
            },(res) => {//失败显示状态码
                console.log(res.status)
            })
            
	        },
	     
	    searchLink(index) {//鼠标点击跳转	
            this.value1 = index;
            window.open('https://www.baidu.com/s?wd=' + this.value1);
            this.value1='';            
        }  
	      
	     },
	   
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import url("../../static/css/find.css");

</style>



