<template>
	<div>
		
        <div class="login-wrap" v-show="showLogin">
        	<router-link to='/mine'>
        	<header>< 返回</header>
            </router-link>
            <router-view></router-view>
            <h3>登录</h3>
            <p v-show="showTishi">{{tishi}}</p>
            <input type="text" placeholder="请输入用户名" v-model="username">
            <input type="password" placeholder="请输入密码" v-model="password">
            <button v-on:click="login" v-model="msg">登录</button>
            <span v-on:click="ToRegister">没有账号？马上注册</span>
        </div>

        <div class="register-wrap" v-show="showRegister">
            <h3>注册</h3>
            <p v-show="showTishi">{{tishi}}</p>
            <input type="text" placeholder="请输入用户名" v-model="newUsername">
            <input type="password" placeholder="请输入密码" v-model="newPassword">
            <button v-on:click="register">注册</button>
            <span v-on:click="ToLogin">已有账号？马上登录</span>
        </div>
    </div>
</template>

<script>
	import {setCookie,getCookie} from '../assets/js/login.js'
	export default {
		 data(){
            return{
                showLogin: true,
                showRegister: false,
                showTishi: false,
                tishi: '',                
                password: '',
                newUsername: '',
                newPassword: '',
                info:'',
                username:'',
                msg:'已登录'
            }
      },
//     mounted(){
//	  /*页面挂载获取cookie，如果存在username的cookie，则跳转到主页，不需登录*/
//	    if(getCookie('username')){
//	        this.$router.push('/find')
//	    }
//	  },
	  methods:{
	    login(){	    	
	        if(this.username == "" || this.password == ""){
	            alert("请输入用户名或密码")
	        }else{
//	            let data = {'username':this.username,'password':this.password}
//	            /*接口请求*/
//	            this.$http.post('http://localhost:8080/b.php',data).then((res)=>{
//	                console.log(res)
//	             /*接口的传值是(-1,该用户不存在),(0,密码错误)，同时还会检测管理员账号的值*/
	              if(this.username!='admin'){
	                  this.tishi = "该用户不存在"
	                  this.showTishi = true
	              }else if(this.username=='admin'&&this.password!=123456){
	                  this.tishi = "密码输入错误"
	                  this.showTishi = true
	              }
//	              else if(res.data == 'admin'){
//            	      /*路由跳转this.$router.push*/
//	                  this.$router.push('/main')
//	              }
	              else if(this.username=='admin'&&this.password==123456){
	                  this.tishi = "登录成功"
	                  this.showTishi = true;
	                  this.$emit('change',this.msg);
	                  console.log(this.msg);
	                  setCookie('username',this.username,1000*60);
	                  
	                  setTimeout(function(){
	                      this.$router.push('/find')
	                  }.bind(this),1000)
	              }
	         
	      }
	    },
	    ToRegister(){
		    this.showRegister = true
		    this.showLogin = false
		},
		ToLogin(){
		    this.showRegister = false
		    this.showLogin = true
		},
		register(){
		    if(this.newUsername == "" || this.newPassword == ""){
		        alert("请输入用户名或密码")
		    }else{
		        let data = {'username':this.newUsername,'password':this.newPassword}
		        this.$http.post('http://localhost/vueapi/index.php/Home/user/register',data).then((res)=>{
		            console.log(res)
		            if(res.data == "ok"){
		                this.tishi = "注册成功"
		                this.showTishi = true
		                this.username = ''
		                this.password = ''
		                 /*注册成功之后再跳回登录页*/
		                setTimeout(function(){
		                    this.showRegister = false
		                    this.showLogin = true
		                    this.showTishi = false
		                }.bind(this),1000)
		            }
		        })
		    }
		}
	  }
	}
</script>

<style scoped>
	@import url("../../static/css/login.css");
	
</style>


