<template>
	<div class="article">
		<header>
			<img src="../assets/img/img20.png" />
		</header>
		<nav>
			<h1>比例偏见：数字不变，想造更大的噱头，怎么办？</h1>
		</nav>
		<section>
			<p>我有个朋友，在消费品公司做促销。有一次，她策划了一个“砸金蛋赢大奖”的活动。
			10个金蛋中的1个，里面只有最新款蓝牙耳机。顾客可以用积分兑换砸金蛋的机会，砸中耳机，
			当场拿走。有不少顾客参与了这个活动，但还是比她预想的要少。她预算有限，不能曾江奖品。
			必须用同样的中奖率，吸引更多顾客参与，怎么办？
			</p>
			<p>要解决这个“怎么办”的问题，我们首先要理解这个问题的本质。这个问题的本质，其实就是
			，怎样才能让同样一个概率，在顾客心中“显得”比实际的大？让一个比率“显得”比实际的大？
			</p>
			<p>问你一个问题。我有两个盒子，一大一小。A盒子小一些，里面有20块饼干，其中1块是巧克力饼干
			；B盒子要大一些，里面有200块饼干，其中有10块是巧克力饼干。如果你想要一块巧克力饼干，你会从
			哪个盒子里拿？
			</p>
			<p>
				从有200块饼干的大盒子里拿？恭喜你，你和其它正常人一样，有“比例偏见”。20块里
				有1块，200块里有10块，只能拿一次的话，你拿到巧克力饼干的概率其实是一模一样的，都是
				1/20.但因为“比例偏见”，人们就是会觉得，从200块的大盒子里拿，“显得”概率会大一些。
			</p>
			<p>
				那我这个朋友应该怎么办？把金蛋从10个变成100个，再把蓝牙耳机从1个变成10个。这就意味着，
				中奖概率从1/10，变成了10/100。哇，100个中间抽10个啊！我运气不会那么差吧。或者呢，也可以在
				100个金蛋中，还是放1个耳机，但允许用户砸10次。中奖概率还是从1/10，变成了10/100，
				但是用户砸十次的参与感，创造了额外的更大的乐趣。
			</p>
			<p>人非机器，对相对大小，和实际大小，常常混淆。你，是哪个少见的清醒者吗？</p>
		</section>
	</div>
</template>

<script>
	export default {
		
	}
</script>

